const teams = require("@microsoft/teams-js/dist/MicrosoftTeams");
const BaseViewElement = require("./base-view");

class CreateStudyGroupViewElement extends BaseViewElement {

    static tagName = 'create-study-group-view';

    constructor() {
        super(CreateStudyGroupViewElement.tagName);

        this.flyoutPanel = this.shadowRoot.querySelector('.flyout-panel');

        this._lightDismissPanel = this.shadowRoot.querySelector('.light-dismiss-panel');
        this._lightDismissPanel.addEventListener('click', this.handleRootClick.bind(this));

        let createButton = this.shadowRoot.querySelector('.create-button');
        createButton.addEventListener('click', this.handleCreateClicked.bind(this));

        let groupNameInput = this.shadowRoot.querySelector('.group-name-input');
        groupNameInput.value = 'New Study Group';
    }

    showAt(x, y) {

        document.body.appendChild(this);
        
        let screenWidth = document.documentElement.scrollWidth;
        x = Math.min(x, screenWidth - this.flyoutPanel.clientWidth);

        this.flyoutPanel.style.marginLeft = `${x}px`;
        this.flyoutPanel.style.marginTop = `${y}px`;
    }

    handleRootClick(e) {
        
        if(e.toElement == this._lightDismissPanel) {
            this.remove();
        }
    }

    handleCreateClicked() {

        let groupNameInput = this.shadowRoot.querySelector('.group-name-input');
        let peoplePicker = this.shadowRoot.querySelector('mgt-people-picker');
        let people = peoplePicker._selectedPeople.map(function(p) {
            return p.userPrincipalName;
        });

        //https://teams.microsoft.com/l/chat/0/0?users=<user1>,<user2>,...&topicName=<chat name>&message=<precanned text>
        let users = encodeURI(people.join(','));
        let topicName = encodeURI(groupNameInput.value);
        let message = encodeURI('Welcome to our new Study Group chat!');
        let createChatUrl = `https://teams.microsoft.com/l/chat/0/0?users=${users}&topicName=${topicName}&message=${message}`;

        let t = setTimeout(function() {
            window.open(createChatUrl);
        }, 1000);

        teams.getContext(function() {
            clearTimeout(t);
            teams.executeDeepLink(createChatUrl);
        });
        
        this.remove();
    }
}

customElements.define(CreateStudyGroupViewElement.tagName, CreateStudyGroupViewElement);
module.exports = CreateStudyGroupViewElement;