const BaseViewElement = require("./base-view");

class ScoreProgressViewElement extends BaseViewElement {

    static tagName = 'score-progress-view';

    constructor() {
        super(ScoreProgressViewElement.tagName);

        let openButton = this.shadowRoot.querySelector('.open-button');
        openButton.addEventListener('click', this.openInVernier);
    }

    openInVernier() {
        console.log("Opening in Vernier app...");
    }
}

customElements.define(ScoreProgressViewElement.tagName, ScoreProgressViewElement);
module.exports = ScoreProgressViewElement;